/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>Back in 2003 I posted on the folly of spending <a href=\"http://www.papascott.de/archives/2003/12/24/espresso-love/\">100s of Euros on a coffee machine</a> and that we were happy with our cheapy plastic machine where we did everything by hand. In the meantime the plastic machine went out, and we switched to a metal machine from AEG where we still did everything by hand.</p>\n<p>Last December at one of MamaMaus' meetings in Munich all mid-managers got a heavy box approximately 50 cm on all sides (like a <a href=\"http://lumma.de/eintrag.php?id=2047\">Dell box</a> folded in half). She didn't even try to take it on the plane with her, she had it shipped to her office. Then it barely fit in her car to bring home. It was a Christmas present, a full-automatic Jura Impressa XF50 espresso machine.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/470857618/\" title=\"Photo Sharing\"><img src=\"https://farm1.static.flickr.com/206/470857618_a2a0a08994.jpg\" width=\"500\" height=\"375\" alt=\"IMG_1982\" /></a></p>\n<p>It's a commercial machine, just like the <a href=\"http://www.fairfax.jura.com/en/ccuk/products_uk/products_home/products_f-line/f50.htm\">retail F50</a> but in black. What we used to lovingly assemble, brew and clean every day was now available at the touch of a button. We got used to the convenience rather quickly. Our daily joke in the morning is now \"Can I make you a cup of coffee\". Since it's absolutely no effort anymore, it's not that much of a favor.</p>\n<p>It's quite large, luckily we have a dead corner on the kitchen counter that it fills up quite nicely. We were afraid that it would be as hard to set up as a video recorder, but it was actually quite easy, and once set up you can pretty much forget it. It even shuts itself off when it's not been used for a few hours. Just do what the screen tells you to do. Fill water, fill beans, empty tray (for water and used grounds). You'll want to rinse out the tray daily, though, if you don't want nasties to grow in it. We use the optional water filters, so we don't have to decalc, instead changing the filter every 2 months. The screen even tells you when to clean. We've done that twice since Christmas, dropping in a tablet and running water through. The filters and tablets are not that expensive, you can get them at Media Markt or on eBay.</p>\n<p>After 4 months with the Jura we're quite happy with it. We still probably wouldn't spend <a href=\"http://www.geizkragen.de/preisvergleich/haushalt/kaffee-und-co/espressomaschinen/jura/jura-impressa-f50/118125.html\">the 800</a> on it ourselves, but it's a nice luxury to have, and we would miss it if we now had to do without.</p>","fields":{"title":"Jura XF50","date":"24 Apr 2007","url":"https://papascott.de/2007-04-24-jura-xf50/"}}},"pathContext":{"slug":"/2007-04-24-jura-xf50/","prev":{"fields":{"title":"Sweater","date":"21 Apr 2007","slug":"/2007-04-21-sweater/"}},"next":{"fields":{"title":"Rock 'n' Roll High School","date":"24 Apr 2007","slug":"/2007-04-24-rock-n-roll-high-school/"}}}}

/***/ })

